body {
  font-family: "Roboto", sans-serif;
}

.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.convert-form {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #e4e4e4;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(34, 34, 34, 0.609);
}
.convert-form p {
  padding: 20px;
  font-size: 1.2rem;
}

.convert-form__input {
  font-size: 1.2rem;
}
.convert-form__input::-webkit-inner-spin-button, .convert-form__input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

.convert-form__button {
  font-size: 1.2rem;
  text-transform: uppercase;
  padding: 5px 10px;
  border-radius: 20px;
  border: none;
  background-color: #fff;
  transition: 0.2s ease-in;
}
.convert-form__button:hover {
  box-shadow: 0 0 10px rgba(34, 34, 34, 0.609);
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}